import mongoose from 'mongoose'
const Schema = mongoose.Schema

const AgentCreditDepositSchema = mongoose.Schema({
	owner:{
		superAgent : { type: Schema.Types.ObjectId, ref: 'SuperAgent' },
		agent : { type: Schema.Types.ObjectId, ref: 'Agent' }
	},
	source: { type: String, required: true },
	status: { type: String, default: 'Deposited' },
	amount: { type: Number, required: true },
	orderNumber: { type: String, required: true },
	createdAt: { type: Date, default: Date.now }
})

const AgentCreditDeposit = mongoose.model('AgentCreditDeposit', AgentCreditDepositSchema)

export default AgentCreditDeposit

